import React from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

toast.configure();

function CustomerNavBar() {
  const history = useHistory();

  const logoutCustomer = () => {
    localStorage.clear();
    toast.info("You've been Logged Out", {
      position: toast.POSITION.BOTTOM_RIGHT,
    });
    history.push("/", null);
  };

  const pushToComponent = (componentName) => {
    history.push({
      pathname: `/customer/${componentName}`,
    });
  };

  const navLinks = [
    {
      name: "Dashobard",
      componentName: "home",
      icon: "fa fa-home fa-2x",
    },
    {
      name: "Create New Orders",
      componentName: "neworder",
      icon: "fa fa-list fa-2x",
    },
    {
      name: "All Orders",
      componentName: "allorders",
      icon: "fa fa-table fa-2x",
    },
    { name: "Profile", componentName: "profile", icon: "fa fa-user fa-2x" },
  ];

  return (
    <>
      <nav className="main-menu">
        <ul>
          {navLinks.map((elem, index) => {
            return (
              <li
                key={index}
                onClick={() => pushToComponent(elem.componentName)}
              >
                <i className={elem.icon}></i>
                <span className="nav-text">{elem.name}</span>
              </li>
            );
          })}
        </ul>

        <ul className="logout" onClick={() => logoutCustomer()}>
          <li>
            <a>
              <i className="fa fa-power-off fa-2x"></i>
              <span className="nav-text">Logout</span>
            </a>
          </li>
        </ul>
      </nav>
    </>
  );
}

export default CustomerNavBar;
