import React from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

toast.configure();

export default function Dashboard(props) {
  const history = useHistory();

  const pushToComponent = (path) => {
    history.push({
      pathname: path,
    });
  };

  const customerDashboardData = [
    { name: "Total Orders", componentPath: "/customer/allorders" },
    { name: "Create Order", componentPath: "/customer/neworder" },
    { name: "User Profile", componentPath: "/customer/profile" },
  ];
  const adminDashboardData = [
    { name: "Total Orders", componentPath: "/admin/viewallorders" },
    { name: "View All Customers", componentPath: "/admin/viewallcustomers" },
    { name: "View All Drafters", componentPath: "/admin/viewalldrafters" },
    { name: "Add Drafter", componentPath: "/admin/adddrafter" },
  ];
  const draterDashboardData = [
    { name: "Assignments", componentPath: "/drafter/allAssignments" },
    { name: "User Profile", componentPath: "/drafter/profile" },
  ];

  return (
    <>
      <div className="dashboard">
        <div id="content">
          <div className="PageHeader">
            <h1>Welcome, {localStorage.getItem(props.userKey)}</h1>
            <p>Choose an item from the menu.</p>
          </div>
          {props.user === "admin" ? (
            <div className="CardsContainer">
              {adminDashboardData.map((adminCards, index) => {
                return (
                  <div
                    key={index}
                    className="Card"
                    style={{ backgroundColor: "#efdeff", color: "black" }}
                    onClick={() => pushToComponent(adminCards.componentPath)}
                  >
                    <h3>
                      {adminCards.name === "Total Orders" ? (
                        <p>{props.totalOrders}</p>
                      ) : null}
                      {adminCards.name}
                    </h3>
                  </div>
                );
              })}
            </div>
          ) : props.user === "customer" ? (
            <div className="CardsContainer">
              {customerDashboardData.map((custCards, index) => {
                return (
                  <div
                    key={index}
                    className="Card"
                    style={{ backgroundColor: "#efdeff", color: "black" }}
                    onClick={() => pushToComponent(custCards.componentPath)}
                  >
                    <h3>
                      {custCards.name === "Total Orders" ? (
                        <p>{props.totalOrders}</p>
                      ) : null}
                      {custCards.name}
                    </h3>
                  </div>
                );
              })}
            </div>
          ) : (
            <div className="CardsContainer">
              <div className="CardsContainer">
                {draterDashboardData.map((drafterCards, index) => {
                  return (
                    <div
                      key={index}
                      className="Card"
                      style={{ backgroundColor: "#efdeff", color: "black" }}
                      onClick={() =>
                        pushToComponent(drafterCards.componentPath)
                      }
                    >
                      <h3>
                        {drafterCards.name === "Assignments" ? (
                          <p>{props.totalTasks}</p>
                        ) : null}
                        {drafterCards.name}
                      </h3>
                    </div>
                  );
                })}
              </div>
              {/* <div
                className="Card"
                style={{ backgroundColor: "#efdeff", color: "black" }}
                onClick={GoToAllTasks}
              >
                <h3>
                  <p>{props.totalTasks}</p>
                  Assignments
                </h3>
              </div>
              <div
                className="Card"
                style={{ backgroundColor: "#efdeff", color: "black" }}
                onClick={GoToUserProfile}
              >
                <h3>User Profile</h3>
              </div> */}
            </div>
          )}
        </div>
      </div>
    </>
  );
}
