import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import CustomerNavBar from "../CustomerNavBar/CustomerNavBar";
import configData from "../../../default.json";
import Dashboard from "../../../components/dashboard/Dashboard";

toast.configure();

function CusromerHome() {
  const history = useHistory();

  const UserNameKey = "CustomerName";
  const CustomerIdKey = "CustomerId";
  const CustomerToken = "CustomerToken";
  const token = localStorage.getItem(CustomerToken);

  const serverString = configData.serverURI;

  const [totalOrders, setTotalOrders] = useState();

  async function loadDataFromAPI() {
    try {
      const orders = await axios.get(
        `${serverString}order/${localStorage.getItem(CustomerIdKey)}`,
        {
          headers: {
            "x-auth-token": `${token}`,
          },
        }
      );
      setTotalOrders(orders.data.length);
    } catch (e) {
      toast.error("Server Down!", { position: toast.POSITION.BOTTOM_RIGHT });
    }
  }
  useEffect(() => {
    if (localStorage.length <= 0) history.push(`/`);
    else loadDataFromAPI();
  });

  return (
    <>
      <CustomerNavBar />

      <div className="dashboard">
        <Dashboard
          user={"customer"}
          totalOrders={totalOrders}
          userKey={UserNameKey}
        />
      </div>
    </>
  );
}

export default CusromerHome;
