import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import axios from "axios";
import "react-toastify/dist/ReactToastify.css";
import AdminNavBar from "../AdminNavBar/AdminNavBar";
import configData from "../../../default.json";
import Dashboard from "../../../components/dashboard/Dashboard";

toast.configure();

function AdminHome() {
  const history = useHistory();

  const AdminNameKey = "AdminName";
  const AdminToken = "AdminToken";
  const token = localStorage.getItem(AdminToken);

  const serverString = configData.serverURI;

  const [totalOrders, setTotalOrders] = useState();

  const loadDataFromAPI = async () => {
    try {
      const orders = await axios.get(`${serverString}order/`, {
        headers: {
          "x-auth-token": `${token}`,
        },
      });

      setTotalOrders(orders.data.length);
    } catch (err) {
      console.log(err);
    }
  };
  useEffect(() => {
    if (localStorage.length <= 0) history.push(`/admin/login`);
    else loadDataFromAPI();
  }, []);

  return (
    <>
      <AdminNavBar />

      <div className="dashboard">
        <div id="content">
          <Dashboard
            user={"admin"}
            totalOrders={totalOrders}
            userKey={AdminNameKey}
          />
        </div>
      </div>
    </>
  );
}

export default AdminHome;
