import React from "react";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./AdminNavBar.css";

toast.configure();

function AdminNavBar() {
  const history = useHistory();

  const logoutCustomer = () => {
    localStorage.clear();
    history.push("/admin/login");
    toast.info("You've been Logged Out", {
      position: toast.POSITION.BOTTOM_RIGHT,
    });
  };

  const pushToComponent = (componentName) => {
    history.push({
      pathname: `/admin/${componentName}`,
    });
  };

  const navLinks = [
    {
      name: "Dashobard",
      componentName: "home",
      icon: "fa fa-home fa-2x",
    },
    {
      name: "All Orders",
      componentName: "viewallorders",
      icon: "fa fa-table fa-2x",
    },
    {
      name: "View All Customers",
      componentName: "viewallcustomers",
      icon: "fa fa-table fa-2x",
    },
    {
      name: "View All Drafters",
      componentName: "viewalldrafters",
      icon: "fa fa-table fa-2x",
    },
    {
      name: "Add a Drafter",
      componentName: "adddrafter",
      icon: "fa fa-user fa-2x",
    },
  ];

  return (
    <>
      <nav className="main-menu">
        <ul>
          {navLinks.map((elem, index) => {
            return (
              <li
                key={index}
                onClick={() => pushToComponent(elem.componentName)}
              >
                <i className={elem.icon}></i>
                <span className="nav-text">{elem.name}</span>
              </li>
            );
          })}
        </ul>

        <ul className="logout">
          <li onClick={logoutCustomer}>
            <a>
              <i className="fa fa-power-off fa-2x"></i>
              <span className="nav-text">Logout</span>
            </a>
          </li>
        </ul>
      </nav>
    </>
  );
}

export default AdminNavBar;
